import InfoTable from "../../../plugin/components/InfoTable";
import PageInfo from "../../../plugin/script/common/PageInfo";
import InfoTableItem from "../../../plugin/components/InfoTableItem";
import InputArea from "../../../plugin/components/InputArea";
export default {
  name: "PayOrderRecord",
  components: {
    InfoTableItem,
    InfoTable,
    InputArea
  },
  data() {
    return {
      pagesize: 5,
      currentPage: 1,
      total: 0,
      dialogTitle: "",
      // tableData: [
      //   {
      //     id: "1",
      //     username: "yoo",
      //     phone: "234",
      //     channel: "e3r"
      //   },
      //   {
      //     id: "2",
      //     username: "yoou",
      //     phone: "234",
      //     channel: "e3r"
      //   }
      // ],
      tableData: [],
      dialogFormVisible: false,
      formLabelWidth: "160px",
      form: {
        vip_class: 0,
        checkList: ["vip头像框", "vip专属炮台"],
        gold_flow: "",
        class_award: "",
        month_money: "",
        vip_tip_text: "",
        probability: "",
        withdraw: "",
        avatar_id: "",
        avatar_name: "",
        avatar_deadline: "",
        use_deadline: "",
        unlock: ""
      },
      fileList: []
    };
  },
  methods: {
    async getVipList() {
      const res = await this.$http.get("lobby/grade", {
        params: {
          page: 1,
          limit: 10
        }
      });
      console.log(res);
      if (res.data.code === 1) {
        this.tableData = res.data.data;
      }
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getUserList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getUserList();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(this.multipleSelection);
    },
    async add() {
      this.dialogTitle = "添加信息";
      this.dialogFormVisible = true;
    },
    async addNewConf() {
      let params = {
        level: 6,
        privilege: "vip",
        charge_coins: 100,
        enter_word: '我进来了',
        caishen_base_rate: 0.8,
        speedup_weight: 0.9,
        icon_url: '头像框',
        avator_id: 1,
        name: 'CIA神',
        icon_type: 1,
        number_day: 30,
        no_condition: 'ide',
        battery_url: 'jeiowfioepjwi',
        img_url: 'www.baidu.com',
        video_url: 'www.baidu.com'
      }
      const res = await this.$http.post('lobby/grade',params)
      console.log(res)
      if(res.data.code === 1) {
        this.dialogFormVisible = false
        this.getVipList()
      }
    },
    handleOffline() {
      this.$confirm("确认更新状态吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "更新成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消更新"
          });
        });
    },
    handleEdit(index, row) {
      console.log(index, row);
      this.dialogFormVisible = true;
      this.dialogTitle = "更新信息";
      this.form.vip_class = row.level
      this.form.gold_flow = row.charge_coins
      this.form.class_award = row.award
      this.form.vip_tip_text = row.enter_word
      this.form.probability = row.caishen_base_rate
      this.form.withdraw = row.speedup_weight
      this.form.avatar_id = row.avator_id
      this.form.avatar_name = row.avator_name
      this.form.unlock = row.no_condition
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm("确认删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    open() {
      this.$confirm("确认发送吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "发送成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消发送"
          });
        });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  },
  mounted() {
    this.getVipList()
  }
};



